<template>
  <div>
    <Loading :active.sync="isLoading"></Loading>
      <div class="row justify-content-center text-center text-monospace">
        <div class="col-2 border" style="background-color:#f5f5f5;">
          <h5>step</h5>
          <h4>01</h4>
          <h5></h5>
        </div>
        <div class="col-2 border mx-2 bg-dark text-white" >
          <h5>step</h5>
          <h4>02</h4>
          <h5></h5>
        </div>
        <div class="col-2 border" style="background-color:#f5f5f5;">
          <h5>step</h5>
          <h4>03</h4>
          <h5>/</h5>
        </div>
      </div>
      <div class="row justify-content-center mt-3">
        <div class="col-8">
          <form @submit.prevent="creatOrder">
            <div class="form-group">
              <label for="useremail">Email</label>
              <input
                type="email"
                class="form-control"
                id="useremail"
                name="email"
                placeholder=" Email"
                v-model="form.user.email"
                v-validate="'required|email'"
                :class="{ 'is-invalid': errors.has('email') }"
              />
              <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
            </div>

            <div class="form-group">
              <label for="username"></label>
              <input
                type="text"
                class="form-control"
                name="name"
                id="username"
                placeholder=""
                v-model="form.user.name"
                v-validate="'required'"
                :class="{ 'is-invalid': errors.has('name') }"
              />
              <span class="text-danger" v-if="errors.has('name')"></span>
            </div>
            <div class="form-group">
              <label for="usertel"></label>
              <input
                type="tel"
                class="form-control"
                id="usertel"
                name="tel"
                placeholder=""
                v-model="form.user.tel"
                v-validate="'required'"
                :class="{ 'is-invalid': errors.has('tel') }"
              />
              <span class="text-danger" v-if="errors.has('tel')"></span>
            </div>
            <div class="form-group">
              <label for="useraddress"></label>
              <input
                type="text"
                class="form-control"
                name="address"
                id="useraddress"
                placeholder=""
                v-model="form.user.address"
                v-validate="'required'"
                :class="{ 'is-invalid': errors.has('address') }"
              />
              <span class="text-danger" v-if="errors.has('address')"></span>
            </div>

            <div class="form-group">
              <label for="comment"></label>
              <textarea
                name
                id="comment"
                class="form-control"
                cols="30"
                rows="10"
                v-model="form.message"
              ></textarea>
            </div>
            <div class="text-right">
              <button class="btn btn-danger" to="/customer_order/customer_checkout"></button>
            </div>
          </form>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      }
    }
  },
  computed: {
    isLoading () {
      return this.$store.state.isLoading
    },
    cart () {
      return this.$store.state.cart
    }
  },
  methods: {
    creatOrder () {
      const vm = this
      const order = vm.form
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_CUSTOMPATH}/order`
      vm.$store.dispatch('updateLoading', true)
      this.$validator.validate().then(result => {
        console.log(result)
        if (result) {
          this.$http.post(api, { data: order }).then(response => {
            console.log('', response.data)
            if (response.data.success) {
              vm.$store.dispatch('updateLoading', false)
              this.$router.push({ name: 'customer_checkout', params: { orderId: response.data.orderId } })
            }
          })
        } else {
          console.log('')
          vm.$store.dispatch('updateLoading', false)
        }
      })
    },
    getCart () {
      this.$store.dispatch('getCart')
    },
    removeCart (id) {
      this.$store.dispatch('removeCart', id)
    }
  }
}
</script>

